import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import auth, { checkAuth } from '../../helpers/auth';
import ClipLoader from 'react-spinners/ClipLoader';
import classes from './CallMe.module.css';

const CallMe = () => {
    const history = useHistory();

    const [loggedUser, setLoggedUser] = useState(false);
    
    useEffect(() => {
        const fetchAuth = async() => {
            if(!auth.isAuthenticated) return history.push('/login', { from: { pathname: history.location.pathname } });
                else {
                    const res = await checkAuth();
                    if(res.status === 1) {
                        setLoggedUser(true); 
                    } else return history.push('/login', { from: { pathname: history.location.pathname } });
                }
        }

        fetchAuth();
    }, [history]);

    
    if(!loggedUser) return <div className={classes.Loading}><ClipLoader size={50} color={'#485877'} /></div>;
    
    return(
        <div className={classes.CallMe}><i className="fas fa-phone"></i> +45 52 71 43 76</div>
    );
}

export default CallMe;
